import "./App.css";
import ReactDOM from "react-dom";

import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Button, CircularProgress } from "@material-ui/core";
import Appbar from "./components/Appbar";
import FileLink from "./components/FileLink";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  button: {
    margin: theme.spacing(1),
  },
  fileButtons: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: theme.palette.grey[100],
  },
  links: {
    display: "flex",
    flexDirection: "column",
  },
}));

function App() {
  const classes = useStyles();
  const [items, handleItems] = useState([]);
  const [isUploading, changeIsUploading] = useState(false);
  const updateItems = () => {
    fetch("/files")
      .then((res) => res.json())
      .then((data) => {
        handleItems(data);
      });
  };

  useEffect(async () => {
    updateItems();
  }, []);
  return (
    <div className={classes.root}>
      <Appbar />
      <form action="/file/upload" method="post" encType="multipart/form-data">
        {isUploading ? (
          <div className={classes.fileButtons}>
            <CircularProgress className={classes.button} />
          </div>
        ) : (
          <div className={classes.fileButtons}>
            <input type="file" name="file" accept="video/*" />

            <Button
              variant="outlined"
              color="primary"
              className={classes.button}
              type="submit"
            >
              Submit
            </Button>
          </div>
        )}
      </form>

      <div className={classes.links}>
        {items &&
          items.map((item) => (
            <FileLink fileName={item.filename} key={item._id} />
          ))}
      </div>
    </div>
  );
}

export default App;
